"use client";
import React, { useState, useEffect } from "react";
import {
  Mail,
  Lock,
  Heart,
  User,
  Droplet,
  Phone,
  Eye,
  EyeOff,
  Sun,
  Moon,
} from "lucide-react";
import { motion } from "framer-motion";

const DonerRegistration = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [bloodType, setBloodType] = useState("");
  const [contact, setContact] = useState("");
  const [focusedInput, setFocusedInput] = useState("");
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [emailError, setEmailError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState({
    length: false,
    number: false,
    uppercase: false,
    lowercase: false,
    special: false,
  });

  const containerVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        when: "beforeChildren",
        staggerChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: {
      opacity: 1,
      x: 0,
      transition: { duration: 0.5 },
    },
  };

  const validateEmail = (email: string) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      setEmailError("Email is required");
    } else if (!emailRegex.test(email)) {
      setEmailError("Please enter a valid email address");
    } else {
      setEmailError("");
    }
  };

  interface PasswordCriteriaProps {
    met: boolean;
    text: string;
  }

  // Define interface for password errors
  interface PasswordErrors {
    length: boolean;
    number: boolean;
    uppercase: boolean;
    lowercase: boolean;
    special: boolean;
  }

  // Password validation
  const validatePassword = (password: string) => {
    setPasswordErrors({
      length: password.length >= 8 && password.length <= 12,
      number: /\d/.test(password),
      uppercase: /[A-Z]/.test(password),
      lowercase: /[a-z]/.test(password),
      special: /[!@#$%^&*(),.?":{}|<>]/.test(password),
    });
  };

  useEffect(() => {
    if (email) validateEmail(email);
  }, [email]);

  useEffect(() => {
    if (password) validatePassword(password);
  }, [password]);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    validateEmail(email);
    validatePassword(password);

    if (!emailError && !passwordErrors) {
      console.log("Registration attempted with:", {
        name,
        email,
        password,
        bloodType,
        contact,
      });
    }
  };

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
  };

  const PasswordCriteria: React.FC<PasswordCriteriaProps> = ({ met, text }) => (
    <div className="flex items-center gap-2">
      <div
        className={`w-2 h-2 rounded-full ${
          met ? "bg-green-500" : "bg-red-500"
        }`}
      />
      <span
        className={`text-sm ${isDarkMode ? "text-gray-300" : "text-gray-600"}`}
      >
        {text}
      </span>
    </div>
  );

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      variants={containerVariants}
      className={`h-screen w-screen ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } flex flex-col justify-center py-12 sm:px-6 lg:px-8`}
    >
      {/* Dark Mode Toggle */}
      <motion.button
        onClick={toggleDarkMode}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg hover:bg-gray-100 transition-colors duration-200"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </motion.button>

      <motion.div
        className="sm:mx-auto sm:w-full sm:max-w-md"
        variants={itemVariants}
      >
        <motion.div
          className="flex justify-center"
          whileHover={{ scale: 1.1, rotate: 360 }}
          whileTap={{ scale: 0.9 }}
          transition={{ duration: 0.5 }}
        >
          <Heart
            className={`w-16 h-16 ${
              isDarkMode ? "text-red-600" : "text-red-500"
            } drop-shadow-lg`}
          />
        </motion.div>
        <motion.h2
          variants={itemVariants}
          className={`mt-6 text-center text-4xl font-bold ${
            isDarkMode ? "text-white" : "text-gray-900"
          } drop-shadow-sm`}
        >
          Donor Registration
        </motion.h2>
        <motion.p
          variants={itemVariants}
          className={`mt-2 text-center text-lg ${
            isDarkMode ? "text-gray-300" : "text-gray-600"
          }`}
        >
          Become a life saver today
        </motion.p>
      </motion.div>

      <motion.div
        variants={containerVariants}
        className="mt-8 sm:mx-auto sm:w-full sm:max-w-md"
      >
        <motion.div
          variants={itemVariants}
          className={`${
            isDarkMode ? "bg-gray-800/80" : "bg-white/80"
          } backdrop-blur-sm py-8 px-4 shadow-2xl sm:rounded-2xl sm:px-10`}
        >
          <form className="space-y-6" onSubmit={handleSubmit}>
            {/* Full Name */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="name"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Full Name
              </label>
              <motion.div
                className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
                  focusedInput === "name"
                    ? "ring-2 ring-red-500 ring-opacity-50"
                    : ""
                }`}
              >
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <User className="h-5 w-5 text-gray-400 " />
                </div>
                <input
                  id="name"
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  onFocus={() => setFocusedInput("name")}
                  onBlur={() => setFocusedInput("")}
                  className={`block w-full pl-10 pr-3 py-3 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 transition-all duration-200`}
                  placeholder="Enter your full name"
                  required
                />
              </motion.div>
            </motion.div>

            {/* Email */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="email"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Email address
              </label>
              <motion.div
                className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
                  focusedInput === "email"
                    ? "ring-2 ring-red-500 ring-opacity-50"
                    : ""
                }`}
              >
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Mail className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  onFocus={() => setFocusedInput("email")}
                  onBlur={() => setFocusedInput("")}
                  className={`block w-full pl-10 pr-3 py-3 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 transition-all duration-200`}
                  placeholder="Enter your email"
                  required
                />
              </motion.div>
              {emailError && (
                <p className="mt-1 text-sm text-red-500">{emailError}</p>
              )}
            </motion.div>

            {/* Password */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="password"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Password
              </label>
              <motion.div
                className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
                  focusedInput === "password"
                    ? "ring-2 ring-red-500 ring-opacity-50"
                    : ""
                }`}
              >
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="password"
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(e) => {
                    setPassword(e.target.value); // Update the password state
                    validatePassword(e.target.value); // Validate the password
                  }}
                  onFocus={() => setFocusedInput("password")}
                  onBlur={() => setFocusedInput("")}
                  className={`block w-full pl-10 pr-12 py-3 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 transition-all duration-200`}
                  placeholder="Create a password"
                  required
                  minLength={6}
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute inset-y-0 right-0 pr-3 flex items-center"
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5 text-gray-400" />
                  ) : (
                    <Eye className="h-5 w-5 text-gray-400" />
                  )}
                </button>
              </motion.div>

              {/* Password validation criteria */}
              {(focusedInput === "password" || password) && (
                <div className="mt-2 space-y-2">
                  <PasswordCriteria
                    met={passwordErrors.length}
                    text="8-12 characters long"
                  />
                  <PasswordCriteria
                    met={passwordErrors.number}
                    text="Contains a number"
                  />
                  <PasswordCriteria
                    met={passwordErrors.uppercase}
                    text="Contains an uppercase letter"
                  />
                  <PasswordCriteria
                    met={passwordErrors.lowercase}
                    text="Contains a lowercase letter"
                  />
                  <PasswordCriteria
                    met={passwordErrors.special}
                    text="Contains a special character"
                  />
                </div>
              )}
            </motion.div>

            {/* Blood Type */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="bloodType"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Blood Type
              </label>
              <motion.div
                className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
                  focusedInput === "bloodType"
                    ? "ring-2 ring-red-500 ring-opacity-50"
                    : ""
                }`}
              >
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Droplet className="h-5 w-5 text-gray-400" />
                </div>
                <select
                  id="bloodType"
                  value={bloodType}
                  onChange={(e) => setBloodType(e.target.value)}
                  onFocus={() => setFocusedInput("bloodType")}
                  onBlur={() => setFocusedInput("")}
                  className={`block w-full pl-10 pr-3 py-3 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 transition-all duration-200 appearance-none`}
                  required
                >
                  <option value="">Select Blood Type</option>
                  <option value="A+">A+</option>
                  <option value="A-">A-</option>
                  <option value="B+">B+</option>
                  <option value="B-">B-</option>
                  <option value="AB+">AB+</option>
                  <option value="AB-">AB-</option>
                  <option value="O+">O+</option>
                  <option value="O-">O-</option>
                </select>
                {/* Custom dropdown arrow */}
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <svg
                    className="h-5 w-5 text-gray-400"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 9l-7 7-7-7"
                    />
                  </svg>
                </div>
              </motion.div>
            </motion.div>

            {/* Contact Number */}
            <motion.div variants={itemVariants}>
              <label
                htmlFor="contact"
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Contact Number
              </label>
              <motion.div
                className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
                  focusedInput === "contact"
                    ? "ring-2 ring-red-500 ring-opacity-50"
                    : ""
                }`}
              >
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Phone className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="contact"
                  type="tel"
                  value={contact}
                  onChange={(e) => setContact(e.target.value)}
                  onFocus={() => setFocusedInput("contact")}
                  onBlur={() => setFocusedInput("")}
                  className={`block w-full pl-10 pr-3 py-3 ${
                    isDarkMode
                      ? "bg-gray-700 text-white"
                      : "bg-white text-gray-900"
                  } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500 transition-all duration-200`}
                  placeholder="Enter contact number"
                  required
                />
              </motion.div>
            </motion.div>

            {/* Submit Button */}
            <motion.div variants={itemVariants}>
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                type="submit"
                className={`w-full py-3 px-4 rounded-lg shadow-sm text-sm font-medium text-white ${
                  isDarkMode
                    ? "bg-gradient-to-r from-red-700 to-red-600"
                    : "bg-gradient-to-r from-red-600 to-red-500"
                } hover:from-red-700 hover:to-red-600 transition-all duration-200`}
              >
                Register Now
              </motion.button>
            </motion.div>
          </form>

          {/* Login Prompt */}
          <motion.div className="mt-6 text-center">
            <p
              className={`text-sm ${
                isDarkMode ? "text-gray-300" : "text-gray-600"
              }`}
            >
              Already have an account?{" "}
              <motion.a
                href="#"
                className={`font-medium ${
                  isDarkMode ? "text-red-400" : "text-red-600"
                } hover:text-red-500 transition-colors duration-200`}
                whileHover={{ scale: 1.05 }}
              >
                Sign in here
              </motion.a>
            </p>
          </motion.div>
        </motion.div>
      </motion.div>
    </motion.div>
  );
};

export default DonerRegistration;
